/**
 * Layout
 */
section,
.section {
  padding: 108px 0;
}

.page-wrapper {
  padding-bottom: 80px;
}

.section-xs {
  padding-top: 70px;
  padding-bottom: 70px;
}

.extra-bg {
  position: relative;
  margin-top: -100px;
}

.extra-bg:before {
  content: "";
  position: absolute;
  left: -70px;
  right: -70px;
  top: -70px;
  height: 250px;
  background-color: #fff;
}

.extra-bg-inner {
  position: relative;
  z-index: 1;
}

.page-box > .container {
  background-color: #fff;
  position: relative;
}

.page-box > .container:before,
.page-box > .container:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 35px;
  background-color: #fff;
}

.page-box > .container:before {
  left: -35px;
}

.page-box > .container:after {
  right: -35px;
}

.page-wrapper.page-box {
  padding-top: 0;
}

.page-wrapper.page-box > .container {
  padding-top: 40px;
  margin-top: -112px;
}

.content-wrapper.pv {
  padding-top: 50px;
  padding-bottom: 50px;
}

@media (min-width: 576px) {
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  section,
  .section {
    padding: 150px 0;
  }

  .page-wrapper {
    padding: 120px 0;
  }

  .container {
    max-width: 1190px;
  }
}

/**
 * Header
 */
.header-main {
  min-height: 10px;
  margin: 0;
  background-color: transparent;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
}

.header-main .header-inner {
  position: relative;
  padding: 10px 15px;
}

.header-main .navbar {
  padding: 0;
}

.header-main .main-logo {
  width: 160px;
  padding-top: 5px;
  padding-bottom: 0;
}

.header-main .navbar-wrapper {
  position: relative;
}

.header-main .navbar-wrapper .navbar {
  float: right;
}

.header-main .main-menu-nav {
  position: relative;
}

/* Main Menu Dropdown */

.header-main .main-nav {
  margin: 0;
  background-color: #333;
}

.header-main .main-nav li {
  position: relative;
}

.header-main .main-nav li a {
  font-size: 12px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  line-height: 1.25;
  padding: 5px 0;
  font-weight: 400;
  position: relative;
}

.header-main .main-nav > li {
  float: left;
}

.header-main .main-nav > li > a {
  line-height: 1;
  font-size: 12px;
  padding: 10px 0;
  padding-right: 15px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  color: #181c40;
}

.header-main .main-nav li ul {
  z-index: 9999;
  position: absolute;
  top: 33px;
  left: 0;
  width: 190px;
  display: none;
  background-color: #181c40;
  padding: 20px;
}

.header-main .main-nav li ul li ul,
.header-bg-white .header-main .main-nav li ul li ul {
  left: 169px;
  top: 0;
}

.header-main .main-nav li a:hover,
.main-nav li ul > li:hover > a {
  color: rgba(255, 255, 255, 1);
}

.header-main .main-nav > li:hover > a,
.header-main .main-nav > li > a:hover {
  color: $accent;
}

/* Mega Menu */

.header-main .main-nav > li.mega-menu {
  position: inherit;
  left: 0;
  color: rgba(255, 255, 255, 0.7);
}

.header-main .main-nav > li.mega-menu ul {
  right: 0;
  position: absolute;
  top: 33px;
  padding: 15px;
  width: 100%;
}

.header-main .main-nav > li.mega-menu ul li a:hover {
  color: rgba(255, 255, 255, 1);
}

.header-main .main-nav > li.mega-menu ul > li:hover > a {
  color: $accent;
}

.header-main .main-nav > li.mega-menu .megamenu-container .arrow-indicator {
  display: none;
}

/* Mini Menu */

.header-main ul.nav-mini-right {
  float: right;
}

.header-main ul.nav-mini-right > li {
  float: left;
  margin-left: 15px;
}

.header-main ul.nav-mini-right > li:last-child {
  margin-left: 0;
}

.header-main ul.nav-mini-right > li > a {
  line-height: 1;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  color: #000;
  position: relative;
}

.header-main ul.nav-mini-right > li > a:hover {
  color: $accent;
}

.header-main ul.nav-mini-right > li > a .icon-font {
  font-size: 18px;
  display: inline-block;
  vertical-align: middle;
  margin-top: -3px;
  margin-right: 3px;
}

/* Menu Arrow */

.header-main .navbar-arrow .arrow-indicator {
  color: #aaa;
}

.header-main .navbar-arrow ul li .arrow-indicator {
  display: inline-block;
  vertical-align: middle;
  font-size: 9px;
  margin-left: 5px;
}

.header-main .navbar-arrow ul li .arrow-indicator:after {
  font-family: "simple-line-icons";
  content: "\e604";
  display: block;
  line-height: 1;
}

.header-main .navbar-arrow ul li ul li .arrow-indicator {
  margin: 0;
  position: absolute;
  top: 16px;
  right: -10px;
}

.header-main .navbar-arrow ul li ul li .arrow-indicator:after {
  content: "\e606";
}

.header-main .navbar-arrow > ul > li:hover .arrow-indicator {
  color: $accent;
}

.header-main .navbar-arrow > ul > li:hover li .arrow-indicator {
  color: #aaa;
}

.header-main .navbar-arrow > ul > li li:hover .arrow-indicator {
  color: #fff;
}

/* Button to open/close collase on mobile for header */

.header-main .btn.btn-toggle {
  font: inherit;
  text-transform: inherit;
  background-color: transparent;
  padding: 0;
  margin: 0;
  letter-spacing: inherit;
  line-height: 1;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
  border: 0;
  margin-left: 15px;
}

.header-main .btn.btn-toggle:before {
  font-family: "Ionicons";
  content: "\f2d7";
  width: 18px;
  height: 18px;
  line-height: 18px;
  display: block;
  font-size: 22px;
}

.header-main .btn.btn-toggle.collapsed:before {
  content: "\f394";
}

