/**
* Horizontal Sticky Navigation
*/
.fullwidth-horizon-sticky {
  position: relative;
  background: #fff;
  border-bottom: 1px solid #e7e7e7;
  border-top: 1px solid #e7e7e7;
}

.fullwidth-horizon-sticky.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  z-index: 1009;
}

.fullwidth-horizon-sticky .fullwidth-horizon-sticky-item {
  transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
}

ul.horizon-sticky-nav {
  margin: 0;
  padding: 0;
}

ul.horizon-sticky-nav > li {
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-right: 25px;
}

ul.horizon-sticky-nav > li a {
  padding: 15px 0;
  line-height: 1;
  display: block;
  position: relative;
  padding-top: 16px;
  margin-bottom: -1px;
  color: #5d5d5d;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1px;
  font-weight: 700;
}

ul.horizon-sticky-nav > li a:hover {
  color: #ff5252;
}

ul.horizon-sticky-nav > li.active a {
  color: #d60d45;
}

.fullwidth-horizon-sticky.fixed .fullwidth-horizon-sticky-item {
  width: 100%;
  border: 0;
}

.fullwidth-horizon-sticky.fixed ul.horizon-sticky-nav {
  border-bottom: 0;
  border-top: 0;
}

.fullwidth-horizon-sticky-item {
  padding: 0;
}

.fullwidth-horizon-sticky.fixed .fullwidth-horizon-sticky-item {
  border: 0;
  padding: 0;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

.fullwidth-horizon-sticky.none-sticky-hide {
  opacity: 0;
  height: 0;
  overflow: hidden;
  border-width: 0;
  o-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.fullwidth-horizon-sticky.none-sticky-hide.fixed {
  opacity: 1;
  height: 46px;
  margin-top: 0;
  border-width: 1px;
}

@media (min-width: 992px) {
  ul.horizon-sticky-nav > li {
    margin-right: 35px;
  }

  .fullwidth-horizon-sticky.fixed {
    top: 75px;
  }

  .fullwidth-horizon-sticky.none-sticky-hide {
    overflow: hidden;
    left: 10000px;
    right: -10000px;
  }

  .fullwidth-horizon-sticky.none-sticky-hide.fixed {
    top: 0;
    left: 0;
    right: 0;
    o-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
  }
}

@media (min-width: 1200px) {
  .fullwidth-horizon-sticky.none-sticky-hide.fixed {
    top: 0;
  }
}

