.bg-white-gradient-top-bottom .bg-gradient-top {
  top: 0;
  background: -moz-linear-gradient(
    top,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0.99) 1%,
    rgba(255, 255, 255, 0) 100%
  );
  /* FF3.6-15 */
