/**
 * Reset
 */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-family: inherit;
  vertical-align: baseline;
  zoom: 1;
}

:focus {
  text-decoration: none;
  outline: none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol:not(.ol) li,
ul:not(.ul) li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

ol.ol,
ul.ul {
  margin-left: 35px;
  margin-bottom: 20px;
}

ol.ol,
ul.ul {
  margin-left: 35px;
  margin-bottom: 20px;
}

ol.ol ol,
ul.ul ul {
  margin: 15px 0;
  margin-left: 30px;
}

ol.ol li {
  list-style-type: decimal;
  list-style-position: outside;
}

ol.ol ol li {
  list-style-type: upper-roman;
  list-style-position: outside;
}

ul.ul li {
  list-style-type: disc;
  list-style-position: outside;
}

ul.ul ul li {
  list-style-type: circle;
  list-style-position: outside;
}

ol.ol li+li,
ul.ul li+li {
  margin-top: 10px;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

* {
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -khtml-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body {
  color: #5f6467;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

img {
  -ms-interpolation-mode: bicubic;
}

object,
embed {
  max-width: 100%;
}

a:hover,
a:focus,
a:visited {
  text-decoration: none;
  outline: none
}

button {
  outline: none !important;
  cursor: pointer
}



/**
* Default Typography
*/

a,
a:visited,
a:focus,
a:active,
a:hover,
:focus {
  text-decoration: none;
  outline: none;
}

a {
  o-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  color: $link-color;
  font-weight: 400;
}

a:hover,
a:focus {
  text-decoration: none;
  o-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  color: darken($link-color, 10%);
}

::selection {
  color: $accent;
  background: #DFEAF7;
}

::-moz-selection {
  color: $accent;
