.form-control-lg {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 18px;
  border-radius: 4px;
}

.form-bg-light {
  background: #F2F2F2;
  border-color: #F2F2F2;
}

.form-round,
.form-group-sm .form-round,
.form-group-lg .form-round {
  border-radius: 50px;
  padding-left: 15px;
  padding-right: 15px;
}

.form-group-lg .form-round {
  padding-left: 20px;
  padding-right: 20px;
}

.form-icon-left,
.form-icon-right {
  position: relative;
}

.form-icon-left .form-control {
  padding-left: 30px;
}

.form-icon-right .form-control {
  padding-right: 30px;
}

.form-icon-left .icon-addon,
.form-icon-right .icon-addon {
  position: absolute;
  bottom: 1px;
  width: 30px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.form-icon-left .icon-addon {
  left: 1px;
}

.form-icon-right .icon-addon {
  right: 1px;
}

.input-group>.form-control,
.input-group>.input-group-addon,
.input-group>.input-group-btn>.btn {
  height: 40px;
  border-radius: 3px
}

.input-group>.input-group-addon {
  border-color: #DEDEDE;
  font-size: 13px;
  padding-top: 7px
}

.input-group-sm>.form-control,
.input-group-sm>.input-group-addon,
.input-group-sm>.input-group-btn>.btn {
  height: 30px;
  font-size: 12px;
  border-radius: 2px;
  letter-spacing: .5px;
  padding-top: 5px
}

.input-group-lg>.form-control,
.input-group-lg>.input-group-addon,
.input-group-lg>.input-group-btn>.btn {
  height: 50px;
  font-size: 18px;
  border-radius: 4px;
  padding-top: 10px
}

.input-group-btn .btn {
  margin: 0;
  height: 40px;
}

.input-group.border .form-control {
  border: 0;
}

.input-group.border .input-group-prepend .input-group-text,
.input-group.border .input-group-append .input-group-text {
  border: 3px;
}

.form-icon-left,
.form-icon-right {
  position: relative;
}

.form-icon-left .form-control {
  padding-left: 35px;
}

.form-icon-right .form-control {
  padding-right: 35px;
}

.form-icon-left .icon-font,
.form-icon-right .icon-font {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  line-height: 1;
}

.form-icon-left .icon-font {
  left: 0;
}

.form-icon-right .icon-font {
  right: 0;
}

.custom-control-input:checked~.custom-control-label::before {
  border-color: $accent;
  background-color: $accent;
}

.custom-control-input:focus~.custom-control-label::before {
  box-shadow: none;
}

.custom-control-input:focus:not(:checked)~.custom-control-label::before {
  border-color: #ABD5BD;
}

.custom-control-label::before {
  top: 3px;
}

.custom-control-label::after {
  top: 3px;
}

.custom-control-input:not(:disabled):active~.custom-control-label::before {
  background-color: $accent;
  border-color: $accent;
}

.custom-control.custom-checkbox+.custom-control.custom-checkbox,
.custom-control.custom-radio+.custom-control.custom-radio {
  margin-top: 3px;
}



